$dark-main: rgb(37, 37, 37);
$search-bar: rgb(234, 234, 234);
* {
    text-decoration: none;
    margin: 0;
    padding: 0;
    color: $dark-main;
}

body {
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100vw;
}

.container {
    max-width: 500px;
    width: 100vw;
    padding: 20px;
    box-sizing: border-box;
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
    .row {
        width: 100%;
        display: flex;
        justify-content: space-between;
        a{
            width: 48%;
            .card-category{
                width: 100%;
                height: 100px;
                box-shadow: 0px 8px 24px 1px rgba(233, 233, 233, 0.84);;
                border-radius: 16px;
                padding: 15px;
                box-sizing: border-box;
                display: flex;
                justify-content: flex-start;
                align-items: flex-end;
                h2{
                    font-size: 14px;
                }
            }  
        }
        
    }
    .search-row{
        width: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center;
        a{
            display: block;
        }
        .search {
            width: 80%;
            input {
                width: 100%;
                border: none;
                padding-left: 15px;
                box-sizing: border-box;
                height: 41px;
                margin: 20px 0;
                font-size: 15px;
                border-radius: 9px;
                background: $search-bar;
                box-shadow: 0px 8px 24px 1px rgba(233, 233, 233, 0.84);
                outline: none;
            }
        }
    }
    
    .card {
        width: 100%;
        display: flex;
        flex-direction: column;
        padding: 15px;
        font-size: 14px;
        box-sizing: border-box;
        border-radius: 16px;
        margin: 5px 0;
        box-shadow: 0px 8px 24px 1px rgba(233, 233, 233, 0.84);
        .card-title-roll {
            font-size: 18px;
            font-weight: 900;
            margin-bottom: 15px;
        }
        .card-name-roll {
            width: 55%;
            display: flex;
            justify-content: space-between;
            font-weight: 500;
            .bold-margin {
                margin-right: 35px;
                color: rgb(170, 170, 170);
            }
        }
    }
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}